import React from 'react';
import { Link } from 'react-router';
import jump  from 'jump.js';

const img = [
  { title: 'About',  srcName: 'about3' , alt:'about jp mamuric', section: 'greeting-section' },
  { title: 'Projects', srcName: 'development' , alt: 'web development', section: 'projects' }
]

class HeroSection extends React.Component {
  render() {
    return (
      <section className='landing' id='landing'>
        <div className='landing__background'>
          <div className='landing__box '>
            <h1 className='heading landing__heading'>JP Mamuric</h1>
            <div className='landing__list'>
              {
                img.map( item => (
                  <div className='landing__item' key={item.title}>
                    <img
                      className='landing__item-icon'
                      src={require(`../../../assets/img/icons/${item.srcName}.png`)}
                      alt={item.alt}
                      onClick={()=>jump(`#${item.section}`)}
                      />
                    <p className='landing__item-text'>{item.title}</p>
                  </div>
                ))
              }
              <Link to='resume' className='landing__item'>
                <img
                  className='landing__item-icon' src={require(`../../../assets/img/icons/resume2.png`)}
                  alt='resume icon' />
                <p className='landing__item-text'>Resume</p>
              </Link>


            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default HeroSection;

 



