/* eslint-disable */
import React from 'react';
import { Link } from 'react-router';


const SoftList = () => {
  const softskills = [
    {
      name : 'Love to Analyze',
      img: 'card__picture--analyze',
    },
    {
      name : 'Creative',
      img: 'card__picture--creative'
    },
    {
      name : 'Innovative',
      img: 'card__picture--innovative',

    },
    {
      name : 'Communication',
      img: 'card__picture--communication',

    },
    {
      name : 'Time Management',
      img: 'card__picture--time'
    },
    {
      name : 'Proactive',
      img: 'card__picture--proactive'
    },
    {
      name : 'Teamwork',
      img: 'card__picture--teamwork'
    },
    {
      name : 'Open Minded',
      img: 'card__picture--open2'
    },
    {
      name : 'Self Motivating',
      img: 'card__picture--selfmotivating'
    },
    {
      name : 'Fast Learner',
      img: 'card__picture--fastlearner2'
    },
    {
      name : 'Story teller',
      img: 'card__picture--storytime',
    },
    {
      name : 'and... Working to Stronger',
      img: 'card__picture--giphy'
    },
  ];

  return (
    <ul className='skill__section-soft'>
      {
        softskills.map ((item, i) => (
            <li key={i} className='card'>
              <div className='card__side card__side--back'>
                <div className={`card__picture card__picture-soft ${item.img}`}/>
              </div>
              <div className={`card__side card__side--front card__side--front-soft`}>
                <div className='card__cta'>
                  <div className='card__title card__title-soft'>
                    {item.name}
                  </div>
                </div>
              </div>
          </li>
        ))
      }
    </ul>
  );
}

export default SoftList;



