import React from 'react';

import SoftList   from './list-softskills';
import HardList   from './list-hardskills';
import flip from '../../../assets/img/icons/undo2.png';

const SkillSection = () => {
  return (
    <section id='skill-section' className='skill__section'>
      <div className='skill__heading-box'>
        <h1 className='heading-1'>Hard Skills</h1>
        <div className='skill__heading-card skill__heading-card-1'>Tap us</div>
        <img src={flip} alt="flip arrow" className='skill__heading-flip'/>
      </div>
      <HardList />
        <div className='skill__heading-box'>
          <h1 className='heading-1'>Soft Skills</h1>
          <div className='skill__heading-card skill__heading-card-2'>We flip</div>
          <img src={flip} alt="flip arrow" className='skill__heading-flip'/>
        </div>
      <SoftList />
    </section>
  );

}




export default SkillSection;



