import React from 'react';

const DesignSection = () => {
    var webDesigns = [
      {
        src: "natureup",
        alt: 'react css floats web app',
        href: 'https://nature-up.firebaseapp.com/',
        cssType: 'Floats'
      },
      {
        src: "fairfare",
        alt: 'react css flexbox web app',
        href: 'https://farefair-demo.firebaseapp.com/',
        cssType: 'Flexbox'
      },
      {
        src: "nexter",
        alt: 'react css grid web app',
        href: 'https://nexterdemo.firebaseapp.com/',
        cssType: 'Grid'
      }
    ];

    return (
      <div id='designs'>
        <h1 className='heading-1 designs__heading'>Design Demos</h1>
        <section className='designs__section-container' id='design-section'>
          <div className='designs__list'>
            { webDesigns.map( (project, i) => {
              const { src, alt, href, cssType } = project;
              return (
                <a href={href} key={i} target='_blank' className='designs__item' >
                  <img className='designs__img' src={require(`../../../assets/img/designs/${src}.png`)} alt={alt} />
                  <p className='designs__text'>Built with CSS <b className='designs__text-bold'>{cssType}</b></p>
                </a>
              );
            })}
          </div>
        </section>
      </div>
    );
}

export default DesignSection;



