import React from 'react';

class GreetingSection extends React.Component {
  render() {
    return (
      <section className='greeting-section' id='greeting-section'>
        <div className='greetings-main-container '>
          <div className='greetings-text-container '>
            <div className='greetings-text'>
              <span className='g1'>Hi!</span>
              <span className='g2'>my name is</span>
              <span className='g3'>JP </span>

              <span className='g5'>Mamuric</span>

              <div className="content">
                <div className="content__container">
                  <p className="content__container__text">
                    I'm a / an
                  </p>

                  <ul className="content__container__list">
                    <li className="content__container__list__item">Developer</li>
                    <li className="content__container__list__item">Engineer</li>
                    <li className="content__container__list__item">Designer</li>
                    <li className="content__container__list__item">Creator</li>
                  </ul>
                </div>
              </div>
              <span className='g6'>I love to build things</span>
              <span className='g8'>Nice to meet you!</span>
            </div>
          </div>
        </div>

      </section>
    );
  }
}

export default GreetingSection;



