import React from 'react'
import jump  from 'jump.js';

const Footer = () => {
  const getYear = () => {
    let date = new Date();
    let year = date.getFullYear();
    return ` ${year} `;
  }
  return (
    <footer className='footer'>
      <p
        className='footer__back'
        onClick={()=>jump('#landing', {duration: 500})}>
        Back to Top
      </p>
      <p className='footer__copyright'>
         <span className='footer__copyright-icon'>&copy; </span>
         <span className='footer__copyright-name'>Jan Peter Mamuric 2014 - {getYear()}</span>
      </p>
    </footer>
  );
}

export default Footer;



