import React from 'react';

const ProjectSection = () => {
    var webProjects = [
      {
        imgUrl: "react-campaign.png",
        alt: 'react campaign web app',
        href: 'https://reactcampaign.herokuapp.com/'
      },
      {
        imgUrl: "react-youtube.png",
        alt: 'youtube like react web app',
        href: 'https://react-tube-c6767.firebaseapp.com/'
      },
      {
        imgUrl: "burger-builder.png",
        alt: 'burger builder web app',
        href: 'https://burger-builder-84fb5.firebaseapp.com/'
      }
    ];

    return (
      <div id='projects'>
        <h1 className='heading-1 project__heading'>Personal Projects </h1>
        <section className='project-section-container flex-it' id='project-section'>
          <div className='web-container flex-it'>
            { webProjects.map( (project, i) => {
              const { imgUrl, alt, href } = project;
              return (
                <a
                  href={href}
                  key={i}
                  target='_blank'
                  rel="noopener"
                  >
                  <img className='project' src={require(`../../../assets/img/projects/${imgUrl}`)} alt={alt} />
                </a>
              );
            })}
          </div>
        </section>
      </div>
    );
}

export default ProjectSection;



