import React from 'react';

const HardList = () => {
  const hardskills = [
    {
      name : 'CSS3/HTML5',
      img: 'card__picture--htmlcss',
      demo: 'card__demo',
      link: 'https://www.w3.org/standards/webdesign/htmlcss'
    },
    {
      name : 'React',
      img: 'card__picture--react',
      link: 'https://facebook.github.io/react/'
    },
    {
      name : 'Redux',
      img: 'card__picture--redux',
      link: 'http://redux.js.org/'
    },
    {
      name : 'Angular',
      img: 'card__picture--angular',
      link: 'https://angularjs.org/'
    },
    {
      name : 'Firebase',
      img: 'card__picture--firebase',
      link: 'https://firebase.google.com/'
    },
    {
      name : 'Nodejs',
      img: 'card__picture--nodejs',
      link: 'https://nodejs.org/en/about/'
    },
    {
      name : 'MongoDB',
      img: 'card__picture--mongodb',
      link: 'https://www.mongodb.com/'
    },
    {
      name : 'GIT',
      img: 'card__picture--git',
      link: 'https://git-scm.com/'
    },
    {
      name : 'Restful APIs',
      img: 'card__picture--api',
      link: 'http://www.restapitutorial.com/'
    },
    {
      name : 'Responsive',
      img: 'card__picture--responsive',
      link: 'https://developer.mozilla.org/en-US/Apps/Progressive/Responsive'
    },
    {
      name : 'Scalable Apps',
      img: 'card__picture--scalable',
      link: ''
    },
    {
      name : 'Working to be Greater',
      img: 'card__picture--goku',
      link: ''
    },
  ];

  return (
    <ul className='skill__section-hard'>
      {
        hardskills.map ((item, i) => (
            <li key={i} className='card'>
              <div className={`card__side card__side--front ${item.demo}`} >
                <div className={`card__picture ${item.img}`}/>
                <h4 className='card__heading'>
                  <span className={`card__heading-span card__heading-span--1`}>
                    {item.name}
                  </span>
                </h4>
              </div>
              <div className={`card__side card__side--back card__side--back-1`}>
                <div className='card__cta'>
                  <a
                    href={item.link}
                    target='_blank'
                    className='btn btn--white'
                    rel="noopener">
                      Learn More about
                    <br />
                    <b className='card__btn-span'>{item.name}</b>
                  </a>
                </div>
              </div>
          </li>
        ))
      }
    </ul>
  );
}

export default HardList;



